const commonUrl = "http://127.0.0.1:5000"; // 

// post
function postRequest(url, data) {
    var promise = new Promise((resolve, reject) => {
        var that = this;
        uni.request({
            url: commonUrl + url,
			// url:url,
            data: data,
            method: "POST",
            header: {
                "content-type": "application/x-www-form-urlencoded",
                // token: uni.getStorageSync("token")
            },
            success: function(res) {
                //
                if (res.statusCode == 200) {
                    resolve(res.data);
                } else {
                    // code
                    //then
                    // resolve
                    //
                    resolve(res.data.msg);
                }
            },
            error: function(e) {
                reject(e);
            }
        });
    });
    return promise;
}

// get
function getRequest(url, data) {
    var promise = new Promise((resolve, reject) => {
        var that = this;
        uni.request({
            url: commonUrl + url,
			// url:url,
            data: data,
            method: "GET",
            dataType: "json",
            // header: {
            //     "content-type": "application/json"
            // },
            success: function(res) {
                // if (res.statusCode == 200) {
                //     resolve(res.data);
                // } else {
                //     resolve(res.data);
                // }
				resolve(res);
            },
            error: function(e) {
                reject(e);
            }
        });
    });
    return promise;
}

module.exports = {
    post: postRequest,
    get: getRequest
};
